


















































































import { Vue, Component } from "vue-property-decorator";
import { State, Mutation } from "vuex-class";
@Component
export default class OnlineRegistration extends Vue {
  @Mutation("Loader/setOverLay") setOverLay!: Function;
  private search: string = "";
  private headers: Object = [
    { text: "", value: "order" },
    { text: "", value: "identifier" },
    { text: "", value: "name" },
    { text: "", value: "birthDate" },
    { text: "", value: "registrationDate" }
  ];
  private loading: Boolean = true;
  private totleRegistration: string = "";
  private nowRegistration: string = "";
  private singleSelect: Boolean = false;
  private desserts: Array<Object> = [];
  process() {
    if (this.singleSelect) {
      var tzoffset = new Date().getTimezoneOffset() * 60000;
      this.desserts = [];
      this.loading = true;
      var currentDate = new Date(Date.now() - tzoffset)
        .toISOString()
        .substr(0, 10);
      var api = "/registration?date=" + currentDate;
      this.axios
        .get(api)
        .then(data => data.data)
        .then(({ registrations }) => {
          this.desserts = registrations;
          this.loading = false;
        })
        .catch(data => {
          this.$toasted.show(``, {
            type: "error",
            position: "top-right",
            duration: 3000
          });
        });
    } else {
      this.desserts = [];
      this.loading = true;
      this.getRegistrationAll();
    }
  }
  created() {
    this.getCurrentRegistrationInfo();
    this.getRegistrationAll();
  }
  nextNum() {
    if (confirm("?")) {
      this.setOverLay(true);

      this.axios
        .get("/registration/next")
        .then(data => data.data)
        .then(({ registrations }) => {
          this.getRegistrationAll();
          this.$toasted.show(``, {
            type: "success",
            position: "top-right",
            duration: 3000
          });
          this.setOverLay(false);
        })
        .catch(data => {
          this.$toasted.show(``, {
            type: "error",
            position: "top-right",
            duration: 3000
          });
        });
    }
  }
  skipNum() {
    if (confirm("?")) {
      this.setOverLay(true);

      this.axios
        .get("/skip")
        .then(data => data.data)
        .then(({ registrations }) => {
          this.getRegistrationAll();
          this.$toasted.show(``, {
            type: "success",
            position: "top-right",
            duration: 3000
          });
          this.setOverLay(false);
        })
        .catch(data => {
          this.$toasted.show(``, {
            type: "error",
            position: "top-right",
            duration: 3000
          });
        });
    }
  }
  getRegistrationAll() {
    this.axios
      .get("/registration")
      .then(data => data.data)
      .then(({ registrations }) => {
        this.desserts = registrations;
        this.loading = false;
      })
      .catch(data => {
        this.$toasted.show(``, {
          type: "error",
          position: "top-right",
          duration: 3000
        });
      });
  }
  getCurrentRegistrationInfo() {
    this.axios
      .get("/registration/order")
      .then(data => data)
      .then(({ data }) => {
        this.totleRegistration += data.total;
        this.nowRegistration += data.order;
      })
      .catch(data => {
        this.$toasted.show(``, {
          type: "error",
          position: "top-right",
          duration: 3000
        });
      });
  }
}
